$arrowColor: #fff;


@mixin arrows ($arrwidth, $arrheight, $color, $Zind) {
    content: '';
    position: absolute;
    width: $arrwidth;
    height: $arrheight;
    background: $color;
    z-index: $Zind;
    border-radius: 6px 6px 0 0;
    
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body{
   min-height: 100vh;
//   justify-content: center;
//    align-items: center;
   background: #091921;
}
canvas{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -560px;
    margin-left: 380px;
}

 ul{
    margin-top: 20px;
    // margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    }
 
ul li {
    list-style: none;
    margin: 0 20px;
    transition: 0.5s;
}    

ul li a {
    display: block;
    position: relative;
    text-decoration: none;
    padding: 5px;
    font-size: 18px;
    font-family: sans-serif;
    color: $arrowColor;
    text-transform: uppercase; 
    transition: 0.5s;
}

ul:hover li a{ 
            transform: scale(1.2);
            opacity: .2;
            filter: blur(5px);
        }
              
 ul li a:hover{
     transform: scale(1.4);
     opacity: 1;
     filter: blur(0);              
 }
 ul li a::before{
     content: '';
     position: absolute;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
     background-color: #ff105e;
     transition: transform 0.5s;
     transform-origin: right;
     transform: scaleX(0);
     z-index: -1;
 }
ul li a:hover::before{
    transition: transform 0.5s;
    transform-origin: left;
    transform: scaleX(1);
}

.welcome{
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
p{
    position: relative;
    font-family: sans-serif;
    color: $arrowColor;
    text-transform: uppercase;
    font-size: 2em;
    letter-spacing: 4px;
    overflow: hidden;
    background: linear-gradient(90deg, #091921, #fff, #091921);
    background-repeat: no-repeat;
    background-size: 80%;
    animation: animate 3s linear infinite;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
@keyframes animate {
    0%{
        background-position: -500%;
    }
    100%{
        background-position: 500%;
    }
}
}



 .fullclock{
     display: flex;
     justify-content: center;
     align-items: center;
     margin-top: 110px;
 }

.clock{
  width: 350px;
  height: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(clock.png);
  background-size: cover;
  border: 4px solid #091921 ;
  border-radius: 50%;
  box-shadow: 0 -15px 15px rgba(255,255,255,0.05),
              inset  0 -15px 15px rgba(255,255,255,0.05),
              0 15px 15px rgba(0,0,0,0.3),
              inset  0 15px 15px rgba(0,0,0,0.3);
}
.clock{
    &:before{
        content: '';
        position: absolute;
        width: 15px;
        height: 15px;
        background: $arrowColor;
        border-radius: 50%;
        z-index: 10000;
    }
}

.clock{
    .hour{
    position: absolute;
    }
    .min{
    position: absolute;
    }
    .sec{
    position: absolute;
    }
}

.clock .hour, .hr{
    width: 160px;
    height: 160px;
}

.clock .min, .mn{
    width: 190px;
    height: 190px;
}
.clock .sec, .sc{
    width: 230px;
    height: 230px;
}
.hr, .mn, .sc{
    display: flex;
    justify-content: center;
    // align-items: center;
    position: absolute;
  border-radius: 50%
}
.hr{
    &:before{
       @include arrows(8px,80px ,#ff105e ,10px);
    }
}
.mn{
    &:before{
        @include arrows(4px,90px, $arrowColor, 11px);
    }
}
.sc{
    &:before{
        @include arrows(2px,150px, $arrowColor, 12px);
    }
}

.faceRec {
display: flex;
justify-content: center;
align-items: center;
      
}